import React from 'react';
import img from '../../images/card-2-background.png';
import './ContactUs.css';

const ContactUs = () => {
  return (
    <div className="contact-us-main">
      <div
        className="contact-us-top-wrapper"
        data-aos="fade-up"
        // data-aos-anchor-placement="center-bottom"
        data-aos-delay="100"
        data-aos-duration="2000"
      >
        <div className="contact-us-right-element">
          <h4>اتصل بنا</h4>

          <h2>لديك سؤال أو طلب؟</h2>
          <p>
            دروس لحلول التعليم عن بُعد والتحول الرقمي للمؤسسات التعليمية، يسرنا
            تقديم المساعدة
            <br />
            <br />
            امتلك مهارات المعلم المبدع وابدأ تطوير ذاتك وتعزيز مهاراتك التقنية!
          </p>
          <br />
          <br />
          <p>info@doroos.ly :يمكنك التواصل عبر البريد الالكتروني</p>
          {/* <div className="contact-us-button-div">
            <button
              data-target="contact-us-modal"
              className="btn modal-trigger blue lighten-3"
            >
              للانضمام
              <i className="material-icons left">assignment_return</i>
            </button>
          </div> */}
        </div>
        <div className="contact-us-left-element">
          <img src={img} alt="" width="100%" height="100%" />
        </div>
      </div>
    </div>
  );
};

export default ContactUs;
